// Style for all nav-popout h1 and ul
.nav-popout-bespoke,
.nav-popout-it,
.nav-popout-marketing,
.nav-popout-telecoms,
.nav-popout-design,
.nav-popout-cyber,
.nav-popout-developer {
    h1 {
        font-size: 2rem;
        margin-bottom: 24px;
        color: $white;
        letter-spacing: 1px;
        font-weight: 600;
        text-transform: uppercase;
        text-align: left; // Ensure h1 is left aligned
    }
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        flex-direction: column;
        gap: 12px;
        align-items: flex-start; // Align list items to the left
        text-align: left; // Ensure text is left aligned

        li a {
            color: $white;
            text-decoration: none;
            font-size: 1.1rem;
            padding: 6px 0;
            transition: text-decoration 0.2s, color 0.2s;
            &:hover,
            &:focus {
                text-decoration: underline;
                color: #fffbe7;
            }
        }
    }
}